// IndexedDB schema for Skylytics offline storage

export interface Passenger {
  id: string
  pnr: string
  lastName: string
  firstName: string
  seatNumber: string
  flightNumber: string
  destination: string
  checkedIn: boolean
  baggageTags: string[]
  timestamp: number
}

export interface BaggageTag {
  id: string
  tagNumber: string
  pnr: string
  passengerName: string
  flightNumber: string
  destination: string
  weight: number
  status: 'pending' | 'synced'
  timestamp: number
}

export interface QueuedAction {
  id: string
  type: 'check-in' | 'baggage-tag' | 'update'
  payload: unknown
  timestamp: number
  status: 'pending' | 'synced' | 'failed'
  retryCount: number
}

export const DB_NAME = 'skylytics-db'
export const DB_VERSION = 1

export const STORES = {
  PASSENGERS: 'passengers',
  BAGGAGE_TAGS: 'baggage-tags',
  QUEUE: 'sync-queue',
} as const
