import { DB_NAME, DB_VERSION, STORES, Passenger, BaggageTag, QueuedAction } from './schema'

class SkylyticsDB {
  private db: IDBDatabase | null = null

  async init(): Promise<void> {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(DB_NAME, DB_VERSION)

      request.onerror = () => reject(request.error)
      request.onsuccess = () => {
        this.db = request.result
        resolve()
      }

      request.onupgradeneeded = (event) => {
        const db = (event.target as IDBOpenDBRequest).result

        // Passengers store
        if (!db.objectStoreNames.contains(STORES.PASSENGERS)) {
          const passengerStore = db.createObjectStore(STORES.PASSENGERS, { keyPath: 'id' })
          passengerStore.createIndex('pnr', 'pnr', { unique: false })
          passengerStore.createIndex('flightNumber', 'flightNumber', { unique: false })
        }

        // Baggage tags store
        if (!db.objectStoreNames.contains(STORES.BAGGAGE_TAGS)) {
          const baggageStore = db.createObjectStore(STORES.BAGGAGE_TAGS, { keyPath: 'id' })
          baggageStore.createIndex('pnr', 'pnr', { unique: false })
          baggageStore.createIndex('tagNumber', 'tagNumber', { unique: true })
        }

        // Sync queue store
        if (!db.objectStoreNames.contains(STORES.QUEUE)) {
          const queueStore = db.createObjectStore(STORES.QUEUE, { keyPath: 'id' })
          queueStore.createIndex('status', 'status', { unique: false })
          queueStore.createIndex('timestamp', 'timestamp', { unique: false })
        }
      }
    })
  }

  async getPassengerByPNR(pnr: string): Promise<Passenger[]> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.PASSENGERS, 'readonly')
      const store = transaction.objectStore(STORES.PASSENGERS)
      const index = store.index('pnr')
      const request = index.getAll(pnr)

      request.onsuccess = () => resolve(request.result)
      request.onerror = () => reject(request.error)
    })
  }

  async addPassenger(passenger: Passenger): Promise<void> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.PASSENGERS, 'readwrite')
      const store = transaction.objectStore(STORES.PASSENGERS)
      const request = store.put(passenger)

      request.onsuccess = () => resolve()
      request.onerror = () => reject(request.error)
    })
  }

  async addBaggageTag(tag: BaggageTag): Promise<void> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.BAGGAGE_TAGS, 'readwrite')
      const store = transaction.objectStore(STORES.BAGGAGE_TAGS)
      const request = store.put(tag)

      request.onsuccess = () => resolve()
      request.onerror = () => reject(request.error)
    })
  }

  async getBaggageTagsByPNR(pnr: string): Promise<BaggageTag[]> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.BAGGAGE_TAGS, 'readonly')
      const store = transaction.objectStore(STORES.BAGGAGE_TAGS)
      const index = store.index('pnr')
      const request = index.getAll(pnr)

      request.onsuccess = () => resolve(request.result)
      request.onerror = () => reject(request.error)
    })
  }

  async addToQueue(action: QueuedAction): Promise<void> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.QUEUE, 'readwrite')
      const store = transaction.objectStore(STORES.QUEUE)
      const request = store.put(action)

      request.onsuccess = () => resolve()
      request.onerror = () => reject(request.error)
    })
  }

  async getPendingQueue(): Promise<QueuedAction[]> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.QUEUE, 'readonly')
      const store = transaction.objectStore(STORES.QUEUE)
      const index = store.index('status')
      const request = index.getAll('pending')

      request.onsuccess = () => resolve(request.result)
      request.onerror = () => reject(request.error)
    })
  }

  async updateQueueStatus(id: string, status: QueuedAction['status']): Promise<void> {
    if (!this.db) throw new Error('Database not initialized')
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(STORES.QUEUE, 'readwrite')
      const store = transaction.objectStore(STORES.QUEUE)
      const getRequest = store.get(id)

      getRequest.onsuccess = () => {
        const action = getRequest.result
        if (action) {
          action.status = status
          const putRequest = store.put(action)
          putRequest.onsuccess = () => resolve()
          putRequest.onerror = () => reject(putRequest.error)
        } else {
          reject(new Error('Action not found'))
        }
      }
      getRequest.onerror = () => reject(getRequest.error)
    })
  }
}

export const db = new SkylyticsDB()
