export function generateBaggageTagNumber(): string {
  const prefix = 'AA'
  const number = Math.floor(Math.random() * 1000000).toString().padStart(6, '0')
  return `${prefix}${number}`
}

export async function seedDatabase(db: typeof import('./db/client').db): Promise<void> {
  for (const passenger of mockPassengers) {
    await db.addPassenger(passenger)
  }
  console.log('[v0] Database seeded with mock passengers')
}
